.mainPic{
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../../../pictures/thailand1.jpg');
    text-align: center;
    background-repeat: no-repeat;
    background-size: cover;
    }
    h1{
    font-family: fantasy;
    font-weight: 600;
    font-size: 40px;
    margin-bottom: 4%;
    margin-top: 2%;
    }
    .buttonChoice{
    background:linear-gradient(rgb(159, 180, 212), rgb(68, 223, 127),rgb(253, 196, 90)) ;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10%;
    box-shadow: 1px 1px 5px black;
    border-radius: 15px;
    width: 20%;
    height: 20%;
    cursor: pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 25px;
    }
    .buttonChoice:hover{
    opacity: 0.4;
    }
    .buttonChoice:focus{
    text-decoration: none;
    outline-style: none;
    }
    .columnfile{
    display: flex;
    flex-direction: row;
    padding: 20px;
    justify-content: center;
    flex-wrap: wrap;
    }
    .columnA{
    width: 30%;
    display: flex;
    flex-direction: column;
    margin-left: 4px;
    margin-right: 4px;
    margin-bottom: 4px;
    }
    .destiny{
    width:100%;
    color: rgb(239, 222, 216);
    padding: 30px;
    margin-top: 8%;
    background-color: rgb(43, 110, 197);
    box-shadow: 2px 2px 5px black;
    border: none;
    border-radius: 15px;
    font-weight: 600;
    font-size: 15px;
    cursor: pointer;
    }
    .destiny:hover {
    background: linear-gradient(rgb(218, 73, 136), rgb(241, 158, 4)) ;
    }
    .BtnModCancel {
    width: 20%;
    position:absolute;
    bottom:5px;
    right:5px;
    color: rgb(239, 222, 216);
    margin-left: 4%;
    margin-top: 40px;
    padding: 10px;
    background: linear-gradient(rgb(36, 20, 27), rgb(114, 96, 218)) ;
    border: none;
    font-weight: 600;
    font-size: 13px;
    cursor: pointer;
    }
    .BtnModCancel:hover {
      background: rgb(255, 129, 10);
    }